export default " ചെറുകിട സംരംഭകർക്ക് വായ്പ നൽകുന്നതിനായി 2015 ഏപ്രിലിൽ കേന്ദ്ര സർക്കാർ ആരംഭിച്ച പദ്ധതിയാണ് മുദ്ര. പ്രധാനമന്ത്രി മുദ്ര യോജന സ്കീം വഴി സംരംഭകർക്ക് മൂലധനവും പ്രവർത്തിക്കാനാവശ്യമായ ലോകാനുകളും നൽകുന്നു.<br><br>ഇതുവരെ <b>കേരളത്തിലെ സംരംഭകർക്ക്</b> നൽകിയ തുക(2015-20): <b>45,041 കോടി രൂപ</b><br>2020 വർഷത്തിൽ മാത്രം പദ്ധതിയുടെ ഗുണഭോക്താക്കൾ കേരളത്തിൽ: <b>21,77,873</b><br><br>- മുദ്ര വെബ്സൈറ്<br><a href=\https://www.mudra.org.in/Home/ShowPDF\>https://www.mudra.org.in/Home/ShowPDF</a><br>"
